import neilModal from '../../components/neil-modal/neil-modal.vue';
export default {
	components: {
		neilModal
	},
	data() {
		return {
			background: ['color1', 'color2', 'color3'],
			indicatorDots: true,
			autoplay: false,
			//  用户协议框的显示隐藏
			show2: false,
			// 获取到的用户信息
			userInfo: {}
		};
	},
	methods: {
		modalTap: function(e) {
			//   展示用户协议框
			this.show2 = true;
		},
		cancelAuth() {
			this.show2 = false;
		},
		// 获取用户信息 API 在小程序可直接使用，在 5+App 里面需要先登录才能调用
		getUserInfo() {
			uni.getUserInfo({
				provider: "weixin",
				success: result => {
					console.log('getUserInfo success', result);
					this.userInfo = result.userInfo;
				},
				fail: error => {
					console.log('getUserInfo fail', error);
					let content = error.errMsg;
					if (~content.indexOf('uni.login')) {
						content = '请在登录页面完成登录操作';
					}

					uni.getSetting({
						success: res => {
							let authStatus = res.authSetting['scope.userInfo'];
							if (!authStatus) {
								uni.showModal({
									title: '授权失败',
									content: 'Hello uni-app需要获取您的用户信息，请在设置界面打开相关权限',
									success: res => {
										if (res.confirm) {
											uni.openSetting();
										}
									}
								});
							} else {
								uni.showModal({
									title: '获取用户信息失败',
									content: '错误原因' + content,
									showCancel: false
								});
							}
						}
					});








				}
			});
		},
		mpGetUserInfo(result) {
			if (result.detail.errMsg !== 'getUserInfo:ok') {
				uni.showModal({
					title: '获取用户信息失败',
					content: '错误原因' + result.detail.errMsg,
					showCancel: false
				});
				return;
			}
			this.userInfo = result.detail.userInfo;
			const that = this;
			uni.login({
				provider: 'weixin',
				success: function(loginRes) {
					uni.setStorageSync('code', loginRes.code);
					that.userInfo.code = loginRes.code;
					console.log(that.userInfo);
					uni.setStorageSync('userinfo', result.detail.userInfo);
					uni.request({
						url: 'http://www.vzoyo.com/api/user/MiniProgramLogin',
						data: {
							code: that.userInfo.code,
							nickname: that.userInfo.nickName,
							headimage: that.userInfo.avatarUrl,
							gender:that.userInfo.gender,
							province:that.userInfo.province,
							city:that.userInfo.city,
							country:that.userInfo.country
						},
						method: 'POST',
						header: {
							'Content-Type': 'application/x-www-form-urlencoded'
						},
						success: res => {
							if(res.data.code != 1){
								uni.showModal({
									title: '登录失败',
									showCancel: false
								});
							}else{
								console.log(res);
								// uni.setStorageSync('token', res.data.userinfo.token)
								uni.setStorageSync("token", res.data.data.userinfo.token)
								// 登陆成功 跳转到tab首页
								uni.navigateBack({
									delta:1
								})
							}
							
						}
					});
				}
			});
		}
	}
};
